<template>
  <el-dialog v-model="dialogVisible" title="自定义Dex" width="420px" custom-class="tokens" @closed="dialogVisible = false"> 
    <div class="dex-title MB-10">名称</div>
    <input type="text" class="dex-input MB-20" placeholder="请输入Dex名称" v-model="dexName">
    <div class="dex-title MB-10">地址</div>
    <input type="text" class="dex-input" placeholder="请输入Dex合约地址" v-model="dexAddress">
    <el-button class="dex-button" @click="confirm">添加</el-button>
  </el-dialog>
</template>

<script>
import { ElNotification } from 'element-plus'
import { mapGetters } from 'vuex'
export default {
  name: 'Tokens',
  data() {
    return {
      dialogVisible: false,
      dexName: '',
      dexAddress: ''
    }
  },
  computed: {
    ...mapGetters(['connectDialogShow', 'currentNetwork', 'currentDex'])
  },
  methods: {
    open() {
      this.dialogVisible = true
    },
    confirm() {
      if (!this.dexName) {
        ElNotification({
          type: 'error',
          title: '温馨提醒',
          message: '请输入Dex名称'
        })
        return
      }
      if (!this.dexAddress) {
        ElNotification({
          type: 'error',
          title: '温馨提醒',
          message: '请输入Dex合约地址'
        })
        return
      }
      if (this.dexAddress.length != 42) {
        ElNotification({
          type: 'error',
          title: '温馨提醒',
          message: '请输入正确的Dex合约地址'
        })
        return
      }
      let dexList = localStorage.getItem(this.currentNetwork.ChainID+'dexList')
      dexList = dexList ? JSON.parse(dexList) : this.currentNetwork.Dex

      const item = dexList.find(({symbol, address}) => {
        if (symbol === this.dexName || address === this.dexAddress) {
          return true
        }
      })
      if (item) {
        ElNotification({
          type: 'error',
          title: '温馨提醒',
          message: '已存在相同名称或合约Dex'
        })
        return
      }
      this.$emit('add', {
        symbol: this.dexName,
        address: this.dexAddress,
        isAdd: true
      })
      ElNotification({
        type: 'success',
        title: '温馨提醒',
        message: '自定义Dex添加成功'
      })
      this.dialogVisible = false
    }
  }
}
</script>

<style lang="scss">
.dex-title{
  color: #fff;
  font-size: 16px;
}
.dex-input{
  background: none;
  width: 100%;
  height: 40px;
  color: #fff;
  border: 1px solid #5D5D5D;
  border-radius: 8px;
  padding: 0 15px;
}

.el-dialog__headerbtn:focus .el-dialog__close, .el-dialog__headerbtn:hover .el-dialog__close{
  color: #FCBE2F !important;
}

.dex-button{
  width: 100%;
  height: 60px;
  background: #FCBE2F !important;
  border: 1px solid #2A2A2A !important;
  border-radius: 18px !important;
  color: #fff !important;
  margin-top: 30px !important;
  display: block !important;
  &:hover, &:focus, &:active{
    background: #FCBE2F;
    color: #fff !important;
    border: none;
  }
}
.tokens {
  background: rgb(39, 38, 44) !important;
  border-radius: 32px !important;
  max-height: 100vh;
  max-width: 100%;
  display: flex;
  flex-direction: column;

  .el-dialog__close {
    font-size: 20px;
    font-weight: bold;
  }
  .el-dialog__title {
    color: #fff;
  }
  .el-dialog__header {
    border-bottom: 1px solid #5d5d5d;
  }
  .el-dialog__body {
    flex: 1;
    height: 100%;
    overflow: hidden;
    padding: 0;
    padding: 30px;
  }
}
</style>
